export default {
    data() {
        return {
            Nom: 'undefined',
            Description: '',
            Modele: '',
            Etapes: ''
        }
    },
    methods: {
        async Submit() {
            const body = {
                Nom: this.Nom,
                Modele: this.Modele,
                Etapes: this.Etapes,
                Description: this.Description,
            }
            const request = await this.$axios.$post('http://localhost:8000/editProcede',body)
            return request
        },
        goBack() {
            this.$router.back()
        }
    }
}
